.reviews-page.page-header {
  margin-bottom: 2px;
  // padding-right: 30px
  padding: 30px 80px;
  @media (max-width: 1400px) {
    padding: 15px; }
  @include media-breakpoint-down(lg) {
    width: calc( 100% - 30px); }
  .dropdown-reviews-company-wrap {
    display: flex;
    align-items: center;
    .dropdown {
      display: block;
      width: fit-content;
      margin: 0 auto;
      @include media-breakpoint-down(sm) {
        margin-bottom: 15px; }
      @media (max-width: 650px) {
        text-align: left; }
      &-toggle {
        display: flex;
        align-items: center;
        background-color: $site-bg;
        border-radius: $border-radius;
        padding: 12px 15px;
        transition: .3s;
        @media (max-width: 1400px) {
          padding: 15px 10px; }
        @media (max-width: 650px) {
          margin: 0 auto; }
        .img-wrap {
          border: $b #7B7B7B;
          width: 64px;
          height: 64px;
          min-width: 64px;
          @media (max-width: 1400px) {
            width: 54px;
            height: 54px;
            min-width: 54px; } }
        .text-wrap {
          display: inline-block;
          padding-left: 15px;
          @media (max-width: 1400px) {
            padding-left: 12px; }
          .name {
            font-size: 17px;
            font-weight: 500;
            line-height: 24px;
            text-align: left;
            @include media-breakpoint-down(lg) {
              font-size: 15px; }
            @include media-breakpoint-down(md) {
              font-size: 14px; }
            @media (max-width: 650px) {
              font-size: 12px; } } }

        .icon-toggle {
          margin-left: 15px;
          transition: .3s;
          @media (max-width: 1400px) {
            margin-left: 10px; } } }
      &-menu {
        left: 0;
        padding-top: 0;
        border-top-right-radius: 0;
        border-top-left-radius: 0; }
      &-item {
        display: flex;
        align-items: center;
        border-bottom: 1px solid $site-bg;
        &:last-child {
          border: none; }
        .img-wrap {
          display: inline-block;
          width: 40px;
          height: 40px;
          margin-right: 10px;
          border-radius: 50%;
          border: $b #7B7B7B;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover; } } } }
    .dropdown.show {
      .dropdown-toggle {
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0; }
      .icon-toggle {
        transform: rotate(45deg); } } }

  /* Round-Chart */
  .rattings-wrap {
    width: fit-content;
    .ratting {
      .amount {
        display: inline-block;
        min-width: 50px;
        text-align: center;
        background-color: $site-bg;
        border-radius: $border-radius-lang;
        margin-left: 10px;
        font-size: 12px;
        font-weight: 500;
        padding: 0 5px; } } }
  .filter-wrap-block {
    width: 100%;
    min-width: 399px;
    @media (max-width: 1300px) {
      min-width: 240px; }
    @include media-breakpoint-down(lg) {
      min-width: auto; }
    @include media-breakpoint-down(sm) {
      text-align: center;
      padding: 15px; }

    .filter-wrap {
      margin-left: auto;
      @include media-breakpoint-down(sm) {
        display: inline-block; }

      .title {
        display: block;
        text-transform: uppercase;
        font-size: 12px;
        line-height: 16px;
        font-weight: 500;
        margin-bottom: 8px;
        @media (max-width: 1400px) {
          margin-top: 5px;
          margin-bottom: 15px; }
        @include media-breakpoint-down(md) {
          display: inline-block;
          margin-right: 8px; }
        @include media-breakpoint-down(sm) {
          display: block;
          text-align: left; } }

      .label-group {
        display: flex;
        @include media-breakpoint-down(md) {
          display: inline-flex;
          flex-wrap: wrap;
          flex-direction: column; }
        @include media-breakpoint-down(sm) {
          display: flex;
          flex-direction: row; }
        .filter-label {
          background-color: $block-bg;
          cursor: pointer;
          border-radius: $border-radius;
          padding: 12px 16px;
          display: inline-block;
          margin-right: 8px;
          font-size: 14px;
          line-height: 17px;
          @media (max-width: 1400px) {
            padding: 10px;
            font-size: 12px; }
          @include media-breakpoint-down(md) {
            padding: 5px 10px;
            margin-right: 0; }
          @include media-breakpoint-down(sm) {
            margin-right: 5px;
            &:last-child {
              margin-right: 0; } } }

        .filter-input {
          display: none; }
        .filter-input:checked + .filter-label {
          background-color: darken($block-bg, 5%); } } } } }

/* === Page-Body === */
.reviews-page.page-body {
  margin-top: 0;
  @media (max-width: 1400px) {
    padding: 15px; }
  @include media-breakpoint-down(lg) {
    width: calc( 100% - 30px); }
  .pay-tariff-wrap {
    position: relative;
    border-radius: $border-radius-reviews;
    border: $b $btn-off-bg;
    padding: 30px 20px;
    .coments-amount {
      display: inline-block;
      position: absolute;
      top: 30px;
      right: 20px;
      background-color: $amount-bg;
      padding: 8px 15px;
      border-radius: $border-radius-amount;
      font-size: 17px;
      font-weight: 500;
      line-height: 24px; }
    .img-wrap {
      max-width: 310px;
      max-height: 235px;
      overflow: hidden;
      margin: 0 auto;
      img {
        width: 100%;
        object-fit: cover;
        object-position: center bottom; } }
    p {
      text-align: center;
      font-size: 17px;
      line-height: 24px;
      font-weight: 500; } }
  .review-wrap {
    margin-bottom: 30px;
    .review-card.red-bg {
      background-color: $reviw-bg-red;
      .user-img {
        background-color: $abr-us-red; } }
    .review-card.yellow-bg {
      background-color: $reviw-bg-yellow;
      .user-img {
        background-color: $abr-us-yellow; } }
    .review-card.green-bg {
      background-color: $reviw-bg-green;
      .user-img {
        background-color: $abr-us-green; } } }
  /* standart white-bg */
  .review-card {
    position: relative;
    background-color: $clear;
    border-radius: $border-radius-reviews;
    border: $b $btn-off-bg;
    padding: 30px 20px;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    @include media-breakpoint-down(sm) {
      max-width: 500px;
      margin: 0 auto; }
    .review-header {
      border-bottom: $b $btn-off-bg;
      padding-bottom: 20px;
      margin-bottom: 30px;
      .menu-toggle {
        position: absolute;
        right: 20px;
        top: 30px;
        width: 30px;
        height: 15px;
        transform: rotate(.25turn);
        font-size: 40px;
        line-height: 0px;
        overflow: hidden;
        padding: 0;
        transition: .3s;
        &::before {
          content: '\2716';
          font-size: 0;
          display: block;
          text-align: center;
          transition: .5s; } }
      .menu-toggle.close {
        font-size: 0;
        &::before {
          font-size: 20px;
          padding-bottom: 2px; } }

      .menu-review.hide {
        max-height: 0;
        max-width: 0;
        border-width: 0; }
      .menu-review {
        position: absolute;
        right: 50px;
        top: 30px;
        border: $b $dark;
        border-radius: $border-radius-lang-wr;
        display: inline-block;
        background-color: $clear;
        overflow: hidden;
        max-height: 100%;
        max-width: 100%;
        z-index: 1;
        transition: .5s;
        .btn-menu-review {
          border-bottom: $b $block-bord;
          &:last-child {
            border-bottom: none; } }
        .btn-delete,
        .btn-decide,
        .btn-return {
          display: block;
          width: 100%;
          padding: 5px 15px 10px;
          text-align: left;
          font-size: 14px;
          line-height: 22px;
          transition: .3s;
          &:hover {
            background-color: lighten($block-bg, 2%); }
          &::before {
            content: '';
            display: inline-block;
            transform: translateY(6px);
            margin-right: 10px;
            width: 24px;
            height: 24px;
            background-size: 100% 100%;
            background-position: center;
            background-repeat: no-repeat; } }
        .btn-delete {
          &:focus {
            background-color: $abr-us-red; }
          &:active {
            background-color: darken($abr-us-red, 5%); } }
        .i-delete {
          &::before {
            background-image: url(../img/icons/trash.svg); } }
        .i-decide {
          &::before {
            background-image: url(../img/icons/tick-square.svg); } }
        .i-return {
          &::before {
            background-image: url(../img/icons/minus-square.svg); } } }

      .user-img {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 62px;
        height: 62px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 15px;
        background-color: $star-fill;
        /* if img empty */
        font-size: 17px;
        font-weight: 500;
        line-height: 24px;
        text-transform: uppercase;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center center; } }
      .text-wrap {
        display: inline-block;
        vertical-align: top;
        .user-name {
          font-size: 15px;
          font-weight: 500;
          line-height: 16px;
          margin-bottom: 10px; } } }

    /* Review-Body */
    .review-body {
      .date {
        margin-bottom: 10px;
        .icon-time {
          margin-bottom: 2px; }
        .date, .time {
          font-size: 12px;
          font-weight: 500;
          line-height: 16px; }
        .time {
          opacity: .5; } }
      .text p {
        font-size: 15px;
        line-height: 24px; } }
    .site-btn-lite {
      margin-top: auto;
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0; } } } }

// ratting stars
.ratting {
  .ratting-stars {
    stroke: $star-stroke; }
  .ratting-stars.ratting-1 {
    .star_1 {
      fill: $star-fill; } }
  .ratting-stars.ratting-2 {
    .star_1, .star_2 {
      fill: $star-fill; } }
  .ratting-stars.ratting-3 {
    .star_1, .star_2, .star_3 {
      fill: $star-fill; } }
  .ratting-stars.ratting-4 {
    .star_1, .star_2, .star_3, .star_4 {
      fill: $star-fill; } }
  .ratting-stars.ratting-5 {
    .star_1, .star_2, .star_3, .star_4, .star_5 {
      fill: $star-fill; } } }

// Review Pagination
.review-pagination-wrap {
  padding-top: 50px;
  @include media-breakpoint-down(sm) {
    padding-top: 30px; }
  .review-pagination {
    display: inline-flex;
    justify-content: center;
    padding: 0;
    li.item {
      margin: 5px;
      padding: 0;
      border-radius: 50%;
      border: 1px solid #d8d8d8;
      @include media-breakpoint-down(sm) {
        display: none;
        &:first-child {
          display: block; } }

      a {
        display: inline-block;
        padding: 0;
        font-size: 16px;
        line-height: 35px;
        width: 35px + 4;
        border-radius: 50%;
        border: 2px solid $clear;
        text-align: center;
        background-color: $clear;
        color: $dark;
        transition: .3s;
        &:hover {
          background-color: $site-bg; } }
      &.active {
        display: block;
        @include media-breakpoint-down(sm) {
          + .item {
            // + .item + .item
            display: block; } }
        a {
          background-color: $btn-bg;
          color: $clear; } } } }

  .arrow-left,
  .arrow-right {
    border: 1px solid #d8d8d8;
    border-radius: 50%;
    margin: 5px;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      padding: 2px;
      border: 2px solid $clear;
      background-color: $clear;
      transition: .3s;
      &:hover {
        background-color: $site-bg; } } } }
