.my-company-page {
  .page-title {
    margin-bottom: 80px;
    @include media-breakpoint-down(lg) {
      margin-bottom: 60px; }
    @include media-breakpoint-down(md) {
      margin-bottom: 40px; } }
  .company-block {
    border: $b $block-bord;
    border-radius: $border-radius-block;
    padding: 30px 15px;
    @include media-breakpoint-down(lg) {
      margin-bottom: 30px; }
    .company-img-wrap {
      position: relative;
      width: 120px;
      height: 120px;
      margin: 0 auto 25px;
      .company-img {
        object-fit: cover;
        object-position: center top;
        border-radius: 50%; }
      .company-settings {
        position: absolute;
        bottom: 0;
        right: 0;
        transition: .5s;
        &:hover {
          transform: rotate(.2turn); }
        &:focus {
          transform: rotate(.4turn); } } }

    .company-name {
      text-align: center;
      margin-bottom: 10px;
      font-weight: 500; }
    .qr-code-wrap {
      width: 110px;
      height: 110px;
      margin: 0 auto 25px;
      .qr-code-link {
        display: block;
        .qr-code-img {
          width: 100%;
          height: auto;
          object-fit: contain; } } }
    .buttons-wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      @include media-breakpoint-down(lg) {
        justify-content: space-evenly; }
      @include media-breakpoint-down(md) {
        justify-content: center; }
      .btn-downl-qr,
      .btn-create-qr {
        border: $b $btn-bord;
        text-align: center;
        width: 100%;
        max-width: 320px;
        padding: 10px 15px;
        border-radius: $border-radius;
        transition: .4s;
        @include media-breakpoint-down(md) {
          max-width: 420px; } }
      .btn-downl-qr {
        margin-bottom: 10px;
        @include media-breakpoint-down(lg) {
          margin-bottom: 0; }
        @include media-breakpoint-down(md) {
          margin-bottom: 10px; }
        .qr-txt {
          font-weight: 500; } }
      .btn-create-qr {
        font-weight: 500; }
      .btn-icon {
        stroke: currentColor;
        stroke-width: 1.5; }
      .btn-downl-qr:hover,
      .btn-create-qr:hover {
        color: $btn-bord;
        background-color: lighten($block-bg, 2%); }
      .btn-downl-qr:focus,
      .btn-create-qr:focus {
        background-color: $block-bg; } } }
  .btn-add-company {
    display: flex;
    justify-content: center;
    margin: 30px auto 0;
    max-width: 750px;
    svg {
      margin-right: 10px; } } }
