.add-company {
  &-page {
    @media (max-width: 1300px) {
      padding: 15px; }
    @include media-breakpoint-down(lg) {
      width: calc( 100% - 30px); }
    @include media-breakpoint-down(md) {
      max-width: 600px; }
    @include media-breakpoint-down(sm) {
      padding: 20px 0; } }

  &-form {
    h3 {
      margin-bottom: 15px; }
    /* form-block = width 50% form */
    label {
      margin-bottom: 0; }
    label.type-text {
      display: inline-block;
      font-size: 14px;
      font-weight: 500;
      line-height: 16px;
      padding-left: 15px;
      margin-bottom: 10px;
      margin-right: 10px;
      cursor: pointer;
      @include media-breakpoint-down(lg) {
        padding-left: 0; } }
    .label-wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-start; }
    .info-link {
      white-space: nowrap;
      @include media-breakpoint-down(sm) {
        margin-bottom: 5px; }
      svg {
        vertical-align: top; } }
    .link-text {
      color: $link-txt;
      font-size: 14px;
      font-weight: 400;
      line-height: 16px;
      vertical-align: top;
      @include media-breakpoint-down(lg) {
        font-size: 12px; } }
    .recomend-text {
      display: block;
      font-size: 14px;
      line-height: 16px;
      margin-bottom: 8px;
      &:last-of-type {
        margin-bottom: 15px; } }
    .quantity-button {
      border: none;
      background-color: $block-bg; }
    [type="submit"] {
      width: 100%; }
    [disabled] {
      background-color: $btn-off-bg;
      border-color: $btn-off-bg;
      color: $disabled-txt; }
    /* quantity */
    .quantity {
      padding: 0;
      margin-left: auto;
      margin-right: 20px;
      @include media-breakpoint-down(sm) {
        margin-left: 0;
        margin-right: auto; } }
    .form-block {
      border-right: $b $block-bg; }
    .form-group {
      background-color: $block-bg;
      padding: 32px 28px;
      border-radius: $border-radius;
      text-align: center;
      margin-bottom: 30px;
      @include media-breakpoint-down(sm) {
        padding: 20px 15px; }
      .logo-requirements {
        font-size: 14px;
        line-height: 16px;
        @include media-breakpoint-down(lg) {
          font-size: 12px; } }


      .dropzone-wrapper {
        position: relative;
        overflow: hidden;
        text-align: center;
        height: 185px;
        padding: 40px 15px 15px;
        border: 1px dashed $dark;
        border-radius: $border-radius-lang-wr;
        background-color: $clear;
        /*  background-size: auto 90% */
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        box-shadow: 0px 4px 8px #bcbcbc3d;
        /*  (x) remove / clear... */
        .remove-preview {
          position: absolute;
          right: 15px;
          top: 15px;
          cursor: pointer;
          display: none;
          z-index: 2;
          &:hover + .dropzone {
            background-color: red; } }
        .dropzone-desc {}
        .dropzone {
          position: absolute;
          left: 0;
          top: 0;
          display: inline-block;
          width: 100%;
          height: 100%;
          padding: 185px 0 0 0;
          overflow: hidden;
          background-color: transparent;
          cursor: cell;
          opacity: .1;
          z-index: 1;
          transition: .4s;
          &:hover {
            background-color: $btn-bg; } }
        /* ERROR messages styles */
        .error-message {
          display: block;
          width: 100%;
          left: 0;
          bottom: -10px;
          font-size: 12px;
          text-align: center; } } }

    .dropzone-wrapper.active {
      border: none;
      .remove-preview {
        display: inline-block; }
      .icon-dropzone {
        display: none; }
      .text-dropzone {
        display: none; } }

    .company-name-group {
      margin-bottom: 20px;
      .input-text {
        flex: auto;
        border: none;
        outline: none;
        font-size: 16px;
        line-height: 24px;
        font-weight: 400; } }


    /*  [type=checkbox] + label */
    [type=checkbox] {
      ~ label {
        display: inline-block;
        width: 46px;
        min-width: 46px;
        height: 24px;
        background-color: lighten($btn-bg, 20%);
        border-radius: $border-radius-check;
        padding: 2px;
        cursor: pointer;
        transition: .3s;
        .on-off {
          position: relative;
          display: inline-block;
          width: 20px;
          height: 20px;
          border-radius: 50%;
          box-shadow: 0px 2px 2px rgba(4, 28, 47, 0.24);
          background-color: $clear;
          /*  animation: off .5s forwards */
          &::before {
            content: '';
            position: absolute;
            display: inline-block;
            left: calc( 50% - 4px );
            top: calc( 50% - 4px );
            height: 8px;
            width: 8px;
            border-radius: 50%;
            border: 1px solid $btn-bg;
            background-color: $clear;
            border-radius: $border-radius-check;
            transition: .5s; } } } }
    [type=checkbox] {
      ~ input {
        color: $plaseh-c;
        pointer-events: none; }
      &:checked {
        ~ label {
          padding-left: 24px;
          background-color: $btn-bg;
          .on-off {
            /*  animation: on .5s forwards */
            &::before {
              left: calc( 50% - 1px );
              width: 2px;
              border-width: 0px;
              border-color: transparent;
              background-color: $btn-bg; } } }
        ~ input {
          color: $dark;
          pointer-events: auto; } } }
    .check-text-field {
      ~ textarea {
        pointer-events: none;
        user-select: none; }
      &.check-text-field:checked {
        ~ textarea {
          pointer-events: auto; } } }

    /*  .need-phone... */
    /*  .duplicate-review... */
    .need-phone-group,
    .duplicate-review-group {
      display: flex;
      align-items: flex-end;
      padding-bottom: 10px;
      border-bottom: $b $block-bg;
      margin-bottom: 20px;
      @include media-breakpoint-down(sm) {
        flex-wrap: wrap; }
      .text {
        padding-right: 15px;
        @include media-breakpoint-down(sm) {
          margin-bottom: 15px;
          width: 100%; }
        span {
          display: block;
          font-size: 12px;
          font-weight: 500;
          line-height: 16px;
          margin-bottom: 5px; }
        p {
          font-size: 14px;
          line-height: 16px;
          margin: 0; } }
      label {
        margin-bottom: 0; } }

    /*  .telegram... */
    .input-group {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      background-color: $block-bg;
      border-radius: $border-radius;
      padding: 11px 15px;
      padding-left: 50px;
      background-repeat: no-repeat;
      background-size: 24px 24px;
      background-position: 15px center;
      margin-bottom: 15px;
      border: $b transparent;
      transition: .5s;
      @include media-breakpoint-down(sm) {
        padding-left: 35px;
        padding-right: 8px;
        background-size: 20px 20px;
        background-position: 10px center; }

      &:last-of-type {
        margin-bottom: 30px; }
      textarea,
      [type=text],
      [type=email],
      [type=tel] {
        width: calc(100% - (46px + 30px));
        padding-right: 10px;
        background-color: transparent;
        border: none;
        outline: none;
        flex: auto;
        resize: none;
        @include media-breakpoint-down(sm) {
          font-size: 12px; }
        &::placeholder {
          color: $plaseh-c;
          transition: .3s; } }
      textarea {
        margin-right: 10px;
        overflow: hidden; }
      .profile-name {
        border-bottom: 1px solid darken($block-bg, 3%);
        margin-right: 10px; } }


    /* ERROR styles */
    .input-group.error {
      position: relative;
      border-color: $error;
      margin-bottom: 30px;
      input {
        color: $error;
        transition: .3s;
        &:focus {
          color: $dark; } }
      .error-message {
        position: absolute;
        font-size: 12px;
        bottom: -20px;
        left: 15px;
        color: $error;
        font-weight: 500;
        @include media-breakpoint-down(sm) {
          left: 0;
          font-size: 11px; } } }

    .company-name-group {
      background-image: url(../img/icons/company-name.svg); }
    .telegram-group {
      background-image: url(../img/icons/telegram.svg); }
    .email-group {
      background-image: url(../img/icons/sms.svg); }
    .text-field-group {
      background-image: url(../img/icons/text.svg);
 }      // background-position-y: 10px
    .google-group {
      background-image: url(../img/icons/google.svg); }
    .facebook-group {
      background-image: url(../img/icons/facebook.svg); }
    .profile-link-group {
      background-image: url(../img/icons/profile-link.svg); } } }


@keyframes on {
  0% {
    background-color: $clear; }
  50% {
    background-color: $abr-us-green; }
  100% {
    background-color: $clear; } }
@keyframes off {
  0% {
    background-color: $clear; }
  50% {
    background-color: $abr-us-red; }
  100% {
    background-color: $clear; } }

