.contact-page {
  margin-bottom: 5px;
  @include media-breakpoint-down(lg) {
    width: calc( 100% - 30px); }
  @include media-breakpoint-down(md) {
    padding: 20px 15px; }
  @include media-breakpoint-down(sm) {
    max-width: 600px;
    text-align: center; }
  .page-title {
    margin-bottom: 50px; }
  h3 {
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
    margin-bottom: 10px;
    img {
      vertical-align: bottom;
      margin-right: 5px; } }
  .row {
    @include media-breakpoint-down(sm) {
      display: inline-block;
      text-align: left; } }
  .contacts-wrap {
    @include media-breakpoint-down(md) {
      margin-bottom: 30px; }
    address,
    a[href^=tel],
    .schedule li {
      font-size: 20px;
      font-weight: 900;
      padding-left: 35px;
      margin-bottom: 0;
      @include media-breakpoint-down(md) {
        padding-left: 0; }
      @include media-breakpoint-down(sm) {
        padding-left: 35px; } }
    .schedule li {
      font-weight: 600; } } }


/*  Feedback-Page */
.feedback-page {
  margin-top: 0;
  padding-bottom: 200px;
  @include media-breakpoint-down(lg) {
    width: calc( 100% - 30px); }
  @include media-breakpoint-down(md) {
    padding: 20px 15px; }
  @include media-breakpoint-down(sm) {
    max-width: 600px; }
  h3 {
    text-align: center;
    max-width: 600px;
    margin: 0 auto 80px;
    @include media-breakpoint-down(sm) {
      margin-bottom: 40px; } }
  .btn {
    &-block {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      @media (max-width: 600px) {
        justify-content: center; } }
    &-wrap {
      width: calc( 50% - 14px );
      @media (max-width: 600px) {
        width: 100%;
        max-width: 350px;
        margin-bottom: 30px; }
      .site-btn-lite {
        display: block; } }
    &-title {
      display: block;
      text-align: center;
      font-size: 22px;
      font-weight: 500;
      line-height: 26px;
      margin-bottom: 15px;
      @include media-breakpoint-down(sm) {
        font-size: 18px;
        margin-bottom: 10px; } } } }
