.log-in_sign-up-page {
  background-color: transparent;
  @media (max-width: 1400px) {
    padding: 50px 0; }
  @include media-breakpoint-down(lg) {
    width: calc( 100% - 30px); }
  @include media-breakpoint-down(md) {
    width: 100%;
    padding: 0; }
  .slider-log-in-wrap {
    text-align: center;
    @include media-breakpoint-down(sm) {
      display: none; }
    .slider-log-in {
      margin-bottom: 50px; }
    .slick-slide {
      height: 500px;
      overflow: hidden;
      cursor: grab;
      @include media-breakpoint-down(lg) {
        height: 400px; }
      @include media-breakpoint-down(sm) {
        height: 300px; }
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center bottom; } }
    .slick-slider-nav {
      display: inline-block;
      .slick-dots {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        li {
          font-size: 0;
          width: 16px;
          height: 16px;
          border-radius: 50%;
          margin: 10px;
          background-color: #A6B2D2;
          transition: 1s; }
        li.slick-active {
          background-color: #3B5498;
          width: 22px;
          height: 22px; } } }
    .slick-prev {
      margin-right: 50px;
      @include media-breakpoint-down(md) {
        margin: 0; } }
    .slick-next {
      margin-left: 50px;
      @include media-breakpoint-down(md) {
        margin: 0; } } }
  .slider-log-in_sign-up {
    .slick-slide {
      /* overflow: hidden (?) */ } } }
.form-log-in_sign-up {
  padding: 80px 72px;
  background-color: $clear;
  border-radius: $border-radius;
  @include media-breakpoint-down(md) {
    padding: 50px 30px; }
  @include media-breakpoint-down(sm) {
    padding: 50px 15px;
    max-width: 400px;
    margin: 0 auto 30px; }
  h2 {
    font-size: 32px;
    font-weight: 400;
    line-height: 40px;
    text-align: center;
    margin-bottom: 20px; }
  label {
    position: relative;
    display: block;
    .label-text {
      display: block;
      font-size: 14px;
      font-weight: 400;
      line-height: 16px;
      margin-bottom: 6px; }
    .error-message {
      position: absolute;
      font-size: 12px;
      bottom: -20px;
      left: 15px;
      color: $error;
      font-weight: 500;
      @include media-breakpoint-down(sm) {
        left: 0;
        font-size: 11px; } }
    input {
      display: block;
      width: 100%;
      background-color: $block-bg;
      border-radius: $border-radius;
      padding: 11px 15px;
      padding-left: 50px;
      background-repeat: no-repeat;
      background-size: 24px 24px;
      background-position: 15px center;
      margin-bottom: 15px;
      border: $b transparent;
      outline: none;
      transition: .5s;
      &.email {
        background-image: url(../img/icons/sms.svg); }
      &.name {
        background-image: url(../img/icons/user.svg); }
      &.company-name {
        background-image: url(../img/icons/company-name_sign-up.svg); }
      &.phone {
        background-image: url(../img/icons/phone.svg); }
      &.error {
        margin-bottom: 25px;
        border-color: $error;
        color: $error;
        &:focus {
          color: $dark; } } }

    /* password */
    .input-wrap {
      display: block;
      position: relative;
      .password {
        background-image: url(../img/icons/lock.svg); }
      .password-eye {
        stroke: $eye;
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer; }
      .password[type=text] {
        background-image: url(../img/icons/lock-open.svg); }
      .password[type=text] {
        ~.password-eye {
          .line {
            stroke: transparent; } } } } }
  .forgot-password {
    display: inline-block;
    font-size: 15px;
    font-weight: 400;
    line-height: 18px;
    margin-bottom: 20px;
    text-decoration: underline; }
  .site-btn {
    margin-bottom: 15px; }
  .have-account {
    display: block;
    text-align: center;
    margin-bottom: 15px; }
  .site-btn-lite {
    display: block;
    margin-bottom: 0; } }
