.settings-page {
  @media (max-width: 1400px) {
    padding: 50px 15px 0; }
  @include media-breakpoint-down(lg) {
    width: calc( 100% - 30px); }
  @include media-breakpoint-down(sm) {
    max-width: 400px; }
  .form-settings-wrap {
    border-right: $b $block-bg;
    .form-settings {
      padding: 0;
      padding-right: 50px;
      @include media-breakpoint-down(lg) {
        padding-right: 0; } } }
  /* input type checkbox (!) */
  .form-new-password-show {
    display: none;
    &:checked {
      + .btn-show-form {
        .text {
          max-width: 0; }
        .to-back {
          max-width: 500px; } }
      ~.form-new-password {
        max-height: 100%; } } }
  /* label */
  .btn-show-form {
    white-space: nowrap;
    cursor: pointer;
    margin-bottom: 20px;
    max-width: 354px;
    .text,
    .to-back {
      display: inline-block;
      overflow: hidden;
      transition: .3s; }
    .text {
      max-width: 500px;
      max-height: 20px; }
    .to-back {
      max-width: 0;
      max-height: 20px; } }

  .form-new-password-wrap {
    padding-left: 50px;
    @include media-breakpoint-down(lg) {
      padding-left: 15px; } }
  /* Form New Password */
  .form-new-password {
    overflow: hidden;
    max-height: 0;
    padding: 0;
    border-radius: 0;
    transition: .5s; } }

