.success-page {
  padding: 150px 90px;
  text-align: center;
  max-width: 875px;
  @include media-breakpoint-down(sm) {
    padding: 60px 15px; }
  .page-title {
    display: inline-block;
    font-weight: 600;
    max-width: 400px;
    margin-bottom: 40px;
    @include media-breakpoint-down(sm) {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 20px; } }
  .text {
    font-size: 24px;
    line-height: 32px;
    font-weight: 400;
    margin: 0;
    @include media-breakpoint-down(sm) {
      font-size: 16px;
      line-height: 24px; }
    a[href^=mail] {
      text-decoration: underline; } }
  .img-wrap {
    max-width: 180px;
    margin: 0 auto 50px;
    @include media-breakpoint-down(sm) {
      max-width: 100px;
      margin: 0 auto 20px; }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center; } }
  .site-btn-lite {
    display: block;
    max-width: 245px;
    margin: 0 auto; } }
