.review-user-page {
  max-width: 415px;
  padding: 30px 0;
  background-color: $site-bg;
  .company-logo {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto 15px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  h3 {
    text-align: center;
    .sub-title {
      display: block;
      font-size: 17px;
      line-height: 24px;
      font-weight: 500; } }
  /* Form */
  .form-review-user {
    text-align: center;
    .stars-wrap {
      position: relative;
      display: flex;
      display: inline-flex;
      justify-content: center;
      flex-direction: row-reverse;
      margin-bottom: 30px;
      .star {
        stroke: $star-stroke;
        fill: transparent;
        transition: .4s; }
      [for^=stars] {
        padding: 8px;
        cursor: pointer; }
      [for^=stars]:hover .star {
        fill: $star-fill; }
      [for^=stars]:hover ~ [for^=stars] .star {
        fill: $star-fill; }
      [type=radio]:checked ~ [for^=stars] .star {
        fill: $star-fill; }
      .error-message {
        white-space: nowrap;
        left: 50%;
        transform: translateX(-50%);
        bottom: -15px; }
      &:hover {
        [type=radio] ~ [for^=stars] .star {
          fill: transparent; }
        [for^=stars]:hover ~ [for^=stars] .star {
          fill: $star-fill; }
        [for^=stars]:hover .star {
          fill: $star-fill; }
        .error-message {
          color: transparent; } } }

    label {
      position: relative;
      display: block;
      font-size: 14px;
      font-weight: 400;
      line-height: 16px;
      margin-bottom: 0;
      text-align: left;
      .label-text {
        display: inline-block;
        margin-bottom: 10px; }
      .error-message {
        bottom: 8px; } }
    textarea,
    input {
      border: none;
      outline: none;
      width: 100%;
      border-radius: $border-radius-lang-wr;
      font-weight: 400;
      margin-bottom: 20px;
      border: $b transparent;
      transition: .4s;
      &::placeholder {
        color: $plaseh-c;
        transition: .3s; }
      &.error {
        margin-bottom: 25px;
        border-color: $error;
        color: $error; }
      &:focus {
        color: $dark; } }


    textarea {
      height: 250px;
      resize: none;
      font-size: 15px;
      padding: 16px;
      line-height: 16px; }
    input {
      font-size: 16px;
      line-height: 24px;
      padding: 12px 15px; } } }

.repeat-review-user-page {
  margin-top: 0;
  padding-top: 0;
  text-align: center;
  .company-logo {
    margin-bottom: 50px; }
  .page-title {
    display: inline-block;
    text-align: center;
    max-width: 300px;
    margin-bottom: 50px;
    .sub-title {
      font-size: 15px;
      padding-top: 30px; } }
  .site-btn-lite {
    display: block;
    background-color: $clear;
    border-color: transparent;
    margin-bottom: 8px;
    &:hover {
      background-color: mix($clear, $site-bg); }
    img {
      margin-right: 5px; } } }


