.tariffs-features-page {
  .page-title {
    margin-bottom: 30px; }
  .your-tariff {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 30px;
    @include media-breakpoint-down(sm) {
      flex-direction: column; }
    .rate-name {
      display: inline-flex;
      font-size: 20px;
      font-weight: 500;
      line-height: 16px;
      background-color: $standart-title-bg;
      border-radius: $border-radius-block;
      padding: 8px 24px 10px;
      margin: 10px; }

    .rate-price,
    .date-finish {
      display: inline-block;
      max-width: 50%;
      @include media-breakpoint-down(sm) {
        max-width: none; } }

    .rate-price {
      margin-right: 15px;
      white-space: nowrap;
      @include media-breakpoint-down(sm) {
        margin-right: 20px; }
      .price-number {
        font-size: 36px;
        font-weight: 700;
        @include media-breakpoint-down(sm) {
          font-size: 26px; } }
      .period {
        font-size: 24px;
        font-weight: 700;
        @include media-breakpoint-down(sm) {
          font-size: 20px; } }
      .amount {
        display: block;
        font-size: 16px; } }
    .date-finish {
      vertical-align: top;
      .date-finish-txt {
        margin-bottom: 0;
        @include media-breakpoint-down(sm) {
          font-size: 14px; }
        time {
          display: flex;
          align-items: center;
          .icon-time {
            margin-right: 10px;
            @include media-breakpoint-down(sm) {
              width: 21px;
              height: 21px;
              margin-right: 8px;
              transition: .3s; } } } } } } }
