body {
  position: relative;
  min-width: 320px;
  font-family: 'Helvetica', sans-serif;
  font-weight: 400;
  background-color: $site-bg;
  color: $text-c;
  font-style: normal;
  font-size: 16px;
  overflow-x: hidden;
