.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: $clear;
  box-shadow: 0px 4px 8px $box-sh;
  max-height: 100vh;
  z-index: 999;
  transition: .5s;
  @include media-breakpoint-down(md) {
    overflow: visible; }
  @include media-breakpoint-down(sm) {
    overflow-y: scroll;
    &::-webkit-scrollbar {
      width: 0px; } }
  .navbar {
    @include media-breakpoint-down(lg) {
      padding: 0; }
    /* LOGO */
    &-logo-link {
      .logo {
        transition: .3s;
        @media (max-width: 1450px) {
          width: 200px; }
        @include media-breakpoint-down(lg) {
          width: 180px; } } }
    &-nav {
      margin-left: auto;
      margin-right: auto;
      @include media-breakpoint-down(lg) {
        display: block;
        text-align: center; } }
    &-collapse {
      @include media-breakpoint-down(lg) {
        order: 2; } }
    .navbar-nav-list {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      @include media-breakpoint-down(lg) {
        display: inline-flex;
        flex-direction: column;
        align-items: flex-start;
        padding-bottom: 10px; }

      .navbar-item {
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        color: $title-c;
        padding: 10px 15px;
        @media (max-width: 1300px) {
          padding: 5px; }
        @include media-breakpoint-down(lg) {
          order: 3;
          margin: 0;
          padding: 15px 0;
          width: 100%;
          text-align: left; }
        .nav-link {
          padding: 10px 15px;
          font-size: 16px;
          font-weight: 400;
          line-height: 24px;
          color: $title-c;
          border-radius: $border-radius;
          transition: .3s;
          @media (max-width: 1360px) {
            padding: 10px;
            font-size: 15px; }
          &:hover {
            background-color: lighten($block-bg, 2%); } }
        .nav-link.active {
          background-color: $block-bg; }
        // Data-Amount-Company
        .nav-link[data-amount-company] {
          &::after {
            content: attr(data-amount-company);
            display: inline-block;
            background-color: $star-fill;
            border-radius: $border-radius-block;
            padding: 3px 20px;
            font-size: 16px;
            margin-left: 5px;
            margin-right: -10px;
            @media (max-width: 1250px) {
              padding: 3px 16px; }
            @include media-breakpoint-down(lg) {
              margin-right: -5px; } } }
        .nav-link {
          &[data-amount-company="0"],
          &[data-amount-company=""] {
            &::after {
              display: none; } } } }

      .navbar-item-tariffs {
        @include media-breakpoint-down(lg) {
          border-bottom: $b $block-bg; } }
      .navbar-item.tariff-plan {
        padding: 8px 16px;
        text-align: center;
        border-radius: $border-radius;
        border: $b $block-bg;
        @include media-breakpoint-down(lg) {
          order: 2;
          margin: 5px 0;
          width: 100%; }
        .date-finish {
          display: block;
          font-size: 14px;
          line-height: 18px;
          text-align: center; } }
      .navbar-item.tariff-plan.demo {
        background-color: $demo-rate-bg; }
      .navbar-item.tariff-plan.standart {
        background-color: $standart-rate-bg;
        border-color: $standart-rate-bg; }
      .navbar-item.tariff-plan.premium {
        background-color: $premium-rate-bg;
        border-color: $premium-rate-bg; }
      .navbar-item.user-dropdown {
        @include media-breakpoint-down(lg) {
          order: 1;
          background-color: darken($site-bg, 3%);
          border-radius: $border-radius;
          text-align: left;
          margin: 0;
          width: 100%;
          padding: 5px;
          .dropdown-toggle {
            display: flex;
            align-items: center;
            background-color: darken($site-bg, 3%);
            .icon-brecket {
              display: none; }
            .img-wrap {
              width: 64px;
              height: 64px;
              margin-right: 20px;
              font-size: 20px; }
            .name {
              font-size: 20px;
              margin-right: 15px; } }
          .dropdown-menu {
            display: none; } } } }

    /* navbar-item lang */
    .navbar-item.btn-lang-group {
      padding: 4px;
      @include media-breakpoint-down(lg) {
        display: none; } }
    .btn-lang-group {
      background-color: $block-bg;
      border-radius: $border-radius-lang-wr;
      padding: 4px;
      margin: 0 20px;
      @media (max-width: 1350px) {
        margin: 0 10px; }
      @include media-breakpoint-down(sm) {
        margin-left: 0; }
      .lang-select {
        background-color: transparent;
        border: none;
        border-radius: $border-radius-lang;
        padding: 8px;
        font-size: 12px;
        font-weight: 500;
        line-height: 16px;
        transition: .3s;
        &:hover {
          background-color: $clear; } }
      .lang-select.active {
        background-color: $clear; } }

    /* Burger */
    &-toggler {
      border: none;
      outline: none;
      padding-right: 0;
      @include media-breakpoint-down(lg) {
        margin-right: 15px; }
      @include media-breakpoint-down(sm) {
        margin-right: 5px; }
      &-1, &-2, &-3 {
        height: 3px;
        width: 40px;
        background-color: $btn-bg;
        border-radius: 10px;
        margin: 3px 0;
        transition: .5s;
        @include media-breakpoint-down(sm) {
          height: 2px;
          width: 30px; } } }

    &-toggler[aria-expanded=true] {
      .navbar-toggler-2 {
        height: 0;
        width: 0;
        margin: 0; }
      .navbar-toggler-1 {
        transform: rotate(.11turn) translateX(7px);
        @include media-breakpoint-down(sm) {
          transform: rotate(.11turn) translateX(6px); } }
      .navbar-toggler-3 {
        transform: rotate(-.11turn) translateX(7px);
        @include media-breakpoint-down(sm) {
          transform: rotate(-.11turn) translateX(6px); } } } } }

